import "./ItemList.css";
import Card from "./Card";
import CostsFilter from "./CostsFilter";
import React, { useState } from "react";
import CostList from "./CostList";
import CostDiagram from "./CostDiagram";
import PieChartComponent from "./PieChartComponent";
import EditCostForm from "./EditCostForm";

function ItemList(props) {
  const currentYear = new Date().getFullYear().toString();
  const [selectedYear, setSelectedYear] = useState(currentYear);
  const [editingCost, setEditingCost] = useState(null);

  const yearChangeHandler = (year) => {
    setSelectedYear(year);
  };

  const filteredCosts = props.costs.filter((cost) => {
    const costDate = new Date(cost.date);
    return costDate.getFullYear().toString() === selectedYear;
  });

  const categoryData = filteredCosts.reduce((acc, cost) => {
    const category = cost.category?.trim() || "Uncategorized";
    const existingCategory = acc.find((item) => item.category === category);
    if (existingCategory) {
      existingCategory.value += cost.price;
    } else {
      acc.push({ category, value: cost.price });
    }
    return acc;
  }, []);

  const editCostHandler = (costId) => {
    const costToEdit = props.costs.find((cost) => cost.id === costId);
    if (!costToEdit) {
      console.error("Cost not found for editing. Cost ID:", costId);
      return;
    }
    setEditingCost(costToEdit);
  };

  const saveEditedCostHandler = async (updatedCost) => {
    if (!updatedCost.id) {
      console.error("Updated cost is missing ID:", updatedCost);
      return;
    }
    await props.onUpdateCost(updatedCost);
    setEditingCost(null);
  };

  return (
    <div>
      {editingCost && (
        <EditCostForm
          editedCost={editingCost}
          onSave={saveEditedCostHandler}
          onCancel={() => setEditingCost(null)}
        />
      )}
      {!editingCost && (
        <Card className="costs">
          <CostsFilter year={selectedYear} onChangeYear={yearChangeHandler} />
          <PieChartComponent data={categoryData} />
          <CostDiagram costs={filteredCosts} />
        </Card>
      )}
      {!editingCost && (
        <CostList
          costs={filteredCosts}
          onDeleteCost={props.onDeleteCost}
          onEdit={editCostHandler}
        />
      )}
    </div>
  );
}

export default ItemList;